.banner {
	display: flex;
	flex-direction: column;
	align-items: center;
	width: 100%;
	position: relative;
	height: 700px;
	padding-top: 50px;
	background-color: #e7816b;
	box-sizing: border-box;
	background-image: url(../../../assets/bg-pattern-hero-home.svg);
	background-repeat: no-repeat;
	background-position: left 90%;
	overflow: hidden;

	.banner-info {
		width: 88%;
		max-width: 400px;
		z-index: 1;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: space-between;
	}

	.banner-info-phone {
		position: relative;
		bottom: 130px;
	}

	.banner-info-title {
		color: #ffffff;
		font-weight: 500;
		line-height: 36px;
		margin-bottom: 20px;
	}

	.banner-info-content {
		color: #ffffff;
		font-weight: 400;
		font-size: 15px;
		line-height: 25px;
	}

	.btn-banner-info {
		margin-top: 15px;
	}
}

@media only screen and (min-width: 768px) {
	.banner {
		border-radius: 15px;
		background-position: 120% 40%;

		.banner-info {
			max-width: 600px;

			.banner-info-title {
				font-size: 48px;
				line-height: 48px;
			}
			.banner-info-content {
				max-width: 400px;
			}
		}
	}
}

@media only screen and (min-width: 1280px) {
	.banner {
		flex-direction: row;
		text-align: start;
		justify-content: space-evenly;
		border-radius: 15px;
		background-position: right top;

		.banner-info {
			align-items: flex-start;
			justify-content: center;
			margin-left: 50px;

			.btn-banner-info {
				margin-top: 25px;
			}
		}
		.banner-info-phone {
			bottom: -120px;
		}
	}
}
