.btn {
	background-color: white;
	color: black;
	border-radius: 5px;
	padding: 10px 15px;
	width: fit-content;
	font-family: inherit;
	font-size: 15px;
	letter-spacing: 1px;
	cursor: pointer;
	border: none;

	&:hover {
		background-color: #ffad9b;
		color: white;
	}
}

a {
	text-decoration: none;
	color: inherit;
}
