.navbar {
	background-color: black;
	text-align: start;
	padding: 20px 0 20px 20px;

	.navbar-link {
		list-style: none;
		padding: 10px;
		a {
			font-size: 24px;
			letter-spacing: 2px;
			text-decoration: none;
			color: white;

			&.active {
				text-decoration: underline;
			}
		}
	}
}

@media only screen and (min-width: 768px) {
	.navbar {
		display: block;
		background-color: transparent;
		padding: 0;

		ul {
			display: flex;
			list-style: none;
			gap: 30px;

			.navbar-link a {
				cursor: pointer;
				text-decoration: none;
				color: #282c34;
				font-size: 14px;
			}
		}
	}
}

@media only screen and (min-width: 1280px) {
	.navbar .navbar-link a:hover {
		text-decoration: underline;
	}
}
